@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header{width:100%;box-shadow:0px 3px 8px 1px rgba(0,0,0,0.15)}h1{margin:0;padding:1rem 0}.list .search{font-family:'Roboto Mono', monospace;display:inline-block;font-size:20px;margin:1rem 0;padding:.3rem 0;width:100%;outline:none;border:none;border-bottom:2px solid #00000063;transition:.2s ease-in-out}.list .search:focus{border-bottom:2px solid #f06c0080;transition:.3s ease-in-out}main button{width:100%;cursor:pointer;border-radius:3px;margin:2rem 0 4rem 0;outline:none;border:none;padding:1rem;font-size:2rem;width:100%;background-color:#f06c00;color:#fff}.list-item{border-radius:3px;cursor:pointer;font-size:1.5rem;padding:.3rem;padding-left:.6rem;margin:.7rem 0;color:#333;background-color:#ddd;transition:.2s ease-in-out;font-weight:bold;-webkit-tap-highlight-color:transparent}.list-item .list-item-name{display:block;margin:0 30px 0 0}.list-item .arrow-container{display:block;float:right}.list-item .arrow-container .arrow{display:block;margin:.5rem;border:solid;border-width:0 3px 3px 0;border-radius:2px;padding:5px;transition:.1s ease-in-out}.list-item .list-item-dropdown{margin:10px 0 30px 0}.list-item .list-item-dropdown .input-units-container{margin-bottom:20px}.list-item .list-item-dropdown .input-units-container p{display:inline-block;margin:0;font-weight:normal}.list-item .list-item-dropdown .input-units-container .input-units{font-family:'Roboto Mono', monospace;font-size:20px;outline:none;border:none;border-radius:3px;margin-top:1rem;width:4rem;text-align:center}.list-item .list-item-dropdown svg{display:block;margin:auto;border-radius:3px}.list-item .units-display{background-color:#fff;color:#333;font-weight:normal;font-size:20px;padding:0 10px;border-radius:3px;max-width:30px}.list-item .react-slidedown.list-item-dropdown{transition-duration:.2s}.in-stock{background-color:#f06c00;color:#fff;transition:.1s ease-in-out}.barcodes{padding-bottom:50px}.barcodes .barcode-units{color:#f06c00}.barcodes svg{display:block;margin:50px auto}.barcodes h2,.barcodes h4{text-align:center}.barcodes h2{margin:0}.barcodes h4{font-weight:normal}html{width:100%}body{font-family:'Roboto Mono'}.container{display:block;width:90%;max-width:800px;margin:0 auto}

